/**
*  
*/
<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-form :inline="true" class="user-search">
      <el-form-item label="">
        <el-input size="small" v-model="searchForm.deptName" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input size="small" v-model="searchForm.deptNo" placeholder=""></el-input>
      </el-form-item>
      <el-form-item>
        <el-button size="small" type="primary" icon="el-icon-search" @click="search"></el-button>
        <el-button size="small" type="primary" icon="el-icon-plus" @click="handleEdit()"></el-button>
      </el-form-item>
    </el-form>
    <!---->
    <el-table size="small" :data="listData" highlight-current-row v-loading="loading" border
      element-loading-text="" style="width: 100%;">
      <el-table-column align="center" type="selection" width="60">
      </el-table-column>
      <el-table-column sortable prop="deptName" label="" width="300">
      </el-table-column>
      <el-table-column sortable prop="deptNo" label="" width="300">
      </el-table-column>
      <el-table-column sortable prop="editTime" label="" width="300">
        <template slot-scope="scope">
          <div>{{ scope.row.editTime | timestampToTime }}</div>
        </template>
      </el-table-column>
      <el-table-column sortable prop="editUser" label="" width="300">
      </el-table-column>
      <el-table-column align="center" label="" min-width="300">
        <template slot-scope="scope">
          <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button>
          <el-button size="mini" type="danger" @click="deleteUser(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <Pagination v-bind:child-msg="pageparm" @callFather="callFather"></Pagination>
    <!--  -->
    <el-dialog :title="title" :visible.sync="editFormVisible" width="30%" @click="closeDialog">
      <el-form label-width="120px" :model="editForm" :rules="rules" ref="editForm">
        <el-form-item label="" prop="deptName">
          <el-input size="small" v-model="editForm.deptName" auto-complete="off" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="deptNo">
          <el-input size="small" v-model="editForm.deptNo" auto-complete="off" placeholder=""></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="closeDialog"></el-button>
        <el-button size="small" type="primary" :loading="loading" class="title"
          @click="submitForm('editForm')"></el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { deptSave, deptDelete } from '../../api/userMG'
import Pagination from '../../components/Pagination'
export default {
  data() {
    return {
      nshow: true, //switch
      fshow: false, //switch
      loading: false, //
      editFormVisible: false, //
      title: '',
      editForm: {
        deptId: '',
        deptName: '',
        deptNo: '',
        token: localStorage.getItem('logintoken')
      },
      searchForm: {
        deptName: '',
        deptNo: '',
      },
      // rules
      rules: {
        deptName: [
          { required: true, message: '', trigger: 'blur' }
        ],
        deptNo: [{ required: true, message: '', trigger: 'blur' }]
      },
      formInline: {
        page: 1,
        limit: 10,
        varLable: '',
        varName: '',
        token: localStorage.getItem('logintoken')
      },
      // 
      seletedata: {
        ids: '',
        token: localStorage.getItem('logintoken')
      },
      userparm: [], //
      listData: [], //
      // 
      pageparm: {
        currentPage: 1,
        pageSize: 10,
        total: 10
      }
    }
  },
  // 
  components: {
    Pagination
  },
  /**
   * 
   */

  /**
   * 
   */
  created() {
    this.getdata(this.formInline)
  },

  /**
   * 
   */
  methods: {
    // 
    getdata(parameter) {
      this.loading = true
      // 
      let res = {
        code: 0,
        msg: null,
        count: 5,
        data: [
          {
            addUser: null,
            editUser: "",
            addTime: 1672838324805,
            editTime: 1672834724805,
            deptId: 1,
            deptName: 'XX',
            deptNo: '1',
            parentId: 1
          },
          {
            addUser: null,
            editUser: "",
            addTime: 1672838124805,
            editTime: 1672835724805,
            deptId: 2,
            deptName: '',
            deptNo: '02',
            parentId: 1
          },
          {
            addUser: null,
            editUser: "",
            addTime: 1672838224805,
            editTime: 1672838724805,
            deptId: 3,
            deptName: '1',
            deptNo: '11',
            parentId: 1
          },
          {
            addUser: null,
            editUser: "",
            addTime: 1672838724805,
            editTime: 1672837724805,
            deptId: 4,
            deptName: '5',
            deptNo: '5',
            parentId: 1
          },
          {
            addUser: null,
            editUser: "",
            addTime: 1672838724805,
            editTime: 1672839724805,
            deptId: 5,
            deptName: 'v',
            deptNo: 'v',
            parentId: 1
          }
        ]
      }
      this.loading = false
      this.listData = res.data
      this.pageparm.currentPage = this.formInline.page
      this.pageparm.pageSize = this.formInline.limit
      this.pageparm.total = res.count
      // 

      /***
       *  
       */

    },
    // 
    callFather(parm) {
      this.formInline.page = parm.currentPage
      this.formInline.limit = parm.pageSize
      this.getdata(this.formInline)
    },
    // 
    search() {

      if (this.searchForm.deptName !== "") {
        this.listData = this.listData.filter((item) => {
          return item.deptName.indexOf(this.searchForm.deptName) > -1
        })
      }
      if (this.searchForm.deptNo !== "") {
        this.listData = this.listData.filter((item) => {
          return item.deptNo.indexOf(this.searchForm.deptNo) > -1
        })
      }
      if (this.searchForm.deptName == "" && this.searchForm.deptNo == "") {
        this.getdata()
      }
    },
    //
    handleEdit: function (index, row) {
      this.editFormVisible = true
      if (row != undefined && row != 'undefined') {
        this.title = ''
        this.editForm.deptId = row.deptId
        this.editForm.deptName = row.deptName
        this.editForm.deptNo = row.deptNo
      } else {
        this.title = ''
        this.editForm.deptId = ''
        this.editForm.deptName = ''
        this.editForm.deptNo = ''
      }
    },
    // 
    submitForm(editData) {
      this.$refs[editData].validate(valid => {
        if (valid) {
          if (this.editForm.deptId) {
            this.listData.filter((item, index) => {
              if (item.deptId == this.editForm.deptId) {
                item.deptName = this.editForm.deptName
                item.deptNo = this.editForm.deptNo
                this.editFormVisible = false
              }
            })

          } else {
            console.log('====================================');
            console.log(new Date().getTime());
            console.log('====================================');
            this.listData.push({

              addUser: null,
              editUser: "",
              addTime: new Date().getTime(),
              editTime: new Date().getTime(),
              deptId: this.listData[this.listData.length - 1].deptId + 1,
              deptName: this.editForm.deptName,
              deptNo: this.editForm.deptNo,
              parentId: 1

            })
            console.log('====================================');
            console.log(this.listData);
            console.log('====================================');
            this.editFormVisible = false
          }

        } else {
          return false
        }
      })
    },
    // 
    deleteUser(index, row) {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(() => {
          this.listData.splice(index, 1)
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
    },
    // 
    closeDialog() {
      this.editFormVisible = false
    }
  }
}
</script>

<style scoped>
.user-search {
  margin-top: 20px;
}

.userRole {
  width: 100%;
}
</style>





